'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Alert } from '@/components/ui/Alert';
import { Github, Mail, Eye, EyeOff } from 'lucide-react';

interface AuthFormProps {
  mode: 'login' | 'register' | 'reset';
  onSubmit: (data: any) => Promise<void>;
  loading?: boolean;
  error?: string;
  success?: string;
}

export function AuthForm({ mode, onSubmit, loading, error, success }: AuthFormProps) {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    confirmPassword: '',
    rememberMe: false,
    acceptTerms: false,
  });
  
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [validationErrors, setValidationErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const errors: Record<string, string> = {};

    // Email validation
    if (!formData.email) {
      errors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      errors.email = 'Please enter a valid email address';
    }

    // Password validation (not for reset mode)
    if (mode !== 'reset') {
      if (!formData.password) {
        errors.password = 'Password is required';
      } else if (formData.password.length < 8) {
        errors.password = 'Password must be at least 8 characters';
      } else if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(formData.password)) {
        errors.password = 'Password must contain uppercase, lowercase, and number';
      }
    }

    // Registration-specific validation
    if (mode === 'register') {
      if (!formData.firstName) {
        errors.firstName = 'First name is required';
      }
      if (!formData.lastName) {
        errors.lastName = 'Last name is required';
      }
      if (formData.password !== formData.confirmPassword) {
        errors.confirmPassword = 'Passwords do not match';
      }
      if (!formData.acceptTerms) {
        errors.acceptTerms = 'You must accept the terms and conditions';
      }
    }

    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    try {
      await onSubmit(formData);
    } catch (err) {
      // Error handling is done by parent component
    }
  };

  const handleInputChange = (field: string, value: string | boolean) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear validation error when user starts typing
    if (validationErrors[field]) {
      setValidationErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const handleOAuthLogin = async (provider: 'google' | 'github') => {
    try {
      // Generate PKCE parameters for security
      const codeVerifier = generateCodeVerifier();
      const codeChallenge = await generateCodeChallenge(codeVerifier);
      const state = generateRandomString(32);
      
      // Store PKCE parameters in sessionStorage for callback
      sessionStorage.setItem('oauth_code_verifier', codeVerifier);
      sessionStorage.setItem('oauth_state', state);
      
      // Build OAuth URL
      const oauthUrl = buildOAuthUrl(provider, codeChallenge, state);
      
      // Redirect to OAuth provider
      window.location.href = oauthUrl;
    } catch (err) {
      console.error(`OAuth login failed:`, err);
      // Show user-friendly error
      setValidationErrors({ oauth: `Failed to connect with ${provider}. Please try again.` });
    }
  };

  // Helper functions for OAuth
  const generateCodeVerifier = (): string => {
    const array = new Uint8Array(32);
    crypto.getRandomValues(array);
    return btoa(String.fromCharCode.apply(null, Array.from(array)))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=/g, '');
  };

  const generateCodeChallenge = async (verifier: string): Promise<string> => {
    const encoder = new TextEncoder();
    const data = encoder.encode(verifier);
    const digest = await crypto.subtle.digest('SHA-256', data);
    return btoa(String.fromCharCode.apply(null, Array.from(new Uint8Array(digest))))
      .replace(/\+/g, '-')
      .replace(/\//g, '_')
      .replace(/=/g, '');
  };

  const generateRandomString = (length: number): string => {
    const array = new Uint8Array(length);
    crypto.getRandomValues(array);
    return btoa(String.fromCharCode.apply(null, Array.from(array)))
      .replace(/[^a-zA-Z0-9]/g, '')
      .substring(0, length);
  };

  const buildOAuthUrl = (provider: 'google' | 'github', codeChallenge: string, state: string): string => {
    const baseUrls = {
      google: 'https://accounts.google.com/o/oauth2/v2/auth',
      github: 'https://github.com/login/oauth/authorize'
    };

    const clientIds = {
      google: process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID || '',
      github: process.env.NEXT_PUBLIC_GITHUB_CLIENT_ID || ''
    };

    const scopes = {
      google: 'openid email profile',
      github: 'user:email'
    };

    const redirectUri = `${window.location.origin}/auth/callback/${provider}`;

    const params = new URLSearchParams({
      client_id: clientIds[provider],
      redirect_uri: redirectUri,
      scope: scopes[provider],
      response_type: 'code',
      state: state,
      code_challenge: codeChallenge,
      code_challenge_method: 'S256'
    });

    return `${baseUrls[provider]}?${params.toString()}`;
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* OAuth Error Display */}
      {validationErrors.oauth && (
        <Alert variant="error" title="OAuth Error">
          {validationErrors.oauth}
        </Alert>
      )}

      {/* Alerts */}
      {error && (
        <Alert variant="error" title="Error">
          {error}
        </Alert>
      )}
      
      {success && (
        <Alert variant="success" title="Success">
          {success}
        </Alert>
      )}

      {/* OAuth Buttons (not for reset mode) */}
      {mode !== 'reset' && (
        <>
          <div className="space-y-3">
            <Button 
              type="button"
              variant="outline" 
              className="w-full" 
              size="lg"
              onClick={() => handleOAuthLogin('github')}
              disabled={loading}
            >
              <Github className="mr-2 h-4 w-4" />
              Continue with GitHub
            </Button>
            <Button 
              type="button"
              variant="outline" 
              className="w-full" 
              size="lg"
              onClick={() => handleOAuthLogin('google')}
              disabled={loading}
            >
              <Mail className="mr-2 h-4 w-4" />
              Continue with Google
            </Button>
          </div>

          {/* Divider */}
          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-300" />
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-2 bg-white text-gray-500">
                or continue with email
              </span>
            </div>
          </div>
        </>
      )}

      {/* Form Fields */}
      <div className="space-y-4">
        {/* Name fields for registration */}
        {mode === 'register' && (
          <div className="grid grid-cols-2 gap-4">
            <Input
              label="First name"
              type="text"
              value={formData.firstName}
              onChange={(e) => handleInputChange('firstName', e.target.value)}
              error={validationErrors.firstName}
              placeholder="John"
              disabled={loading}
              required
            />
            <Input
              label="Last name"
              type="text"
              value={formData.lastName}
              onChange={(e) => handleInputChange('lastName', e.target.value)}
              error={validationErrors.lastName}
              placeholder="Doe"
              disabled={loading}
              required
            />
          </div>
        )}

        {/* Email field */}
        <Input
          label="Email address"
          type="email"
          value={formData.email}
          onChange={(e) => handleInputChange('email', e.target.value)}
          error={validationErrors.email}
          placeholder={mode === 'reset' ? 'Enter your email to reset password' : 'Enter your email'}
          disabled={loading}
          required
        />

        {/* Password fields (not for reset mode) */}
        {mode !== 'reset' && (
          <>
            <div className="relative">
              <Input
                label="Password"
                type={showPassword ? 'text' : 'password'}
                value={formData.password}
                onChange={(e) => handleInputChange('password', e.target.value)}
                error={validationErrors.password}
                placeholder={mode === 'register' ? 'Create a strong password' : 'Enter your password'}
                disabled={loading}
                helperText={mode === 'register' ? 'Must be at least 8 characters with uppercase, lowercase, and number' : undefined}
                required
              />
              <button
                type="button"
                className="absolute right-3 top-8 text-gray-400 hover:text-gray-600"
                onClick={() => setShowPassword(!showPassword)}
              >
                {showPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>

            {/* Confirm password for registration */}
            {mode === 'register' && (
              <div className="relative">
                <Input
                  label="Confirm password"
                  type={showConfirmPassword ? 'text' : 'password'}
                  value={formData.confirmPassword}
                  onChange={(e) => handleInputChange('confirmPassword', e.target.value)}
                  error={validationErrors.confirmPassword}
                  placeholder="Confirm your password"
                  disabled={loading}
                  required
                />
                <button
                  type="button"
                  className="absolute right-3 top-8 text-gray-400 hover:text-gray-600"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                </button>
              </div>
            )}
          </>
        )}

        {/* Remember me for login */}
        {mode === 'login' && (
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              <input
                id="remember-me"
                name="remember-me"
                type="checkbox"
                checked={formData.rememberMe}
                onChange={(e) => handleInputChange('rememberMe', e.target.checked)}
                className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                disabled={loading}
              />
              <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-700">
                Remember me
              </label>
            </div>
          </div>
        )}

        {/* Terms acceptance for registration */}
        {mode === 'register' && (
          <div className="flex items-start">
            <input
              id="accept-terms"
              name="accept-terms"
              type="checkbox"
              checked={formData.acceptTerms}
              onChange={(e) => handleInputChange('acceptTerms', e.target.checked)}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded mt-0.5"
              disabled={loading}
              required
            />
            <label htmlFor="accept-terms" className="ml-2 block text-sm text-gray-700">
              I agree to the{' '}
              <a href="/terms" className="text-blue-600 hover:text-blue-500">
                Terms of Service
              </a>{' '}
              and{' '}
              <a href="/privacy" className="text-blue-600 hover:text-blue-500">
                Privacy Policy
              </a>
            </label>
            {validationErrors.acceptTerms && (
              <p className="text-sm text-red-600 mt-1">{validationErrors.acceptTerms}</p>
            )}
          </div>
        )}
      </div>

      {/* Submit Button */}
      <Button 
        type="submit" 
        className="w-full" 
        size="lg" 
        loading={loading}
        disabled={loading}
      >
        {mode === 'login' && 'Sign in'}
        {mode === 'register' && 'Create Account'}
        {mode === 'reset' && 'Send Reset Link'}
      </Button>
    </form>
  );
}